:root {
  --peru-red: #D91023;
  --peru-yellow: #FCD116;
  --peru-blue: #007EA8;
}

.word-card {
	background-color: white;
	margin: 0.7em;
	padding: 2em 3em;
	border-radius: 1em;
	border: 2px solid var(--peru-yellow) ;
	width: 40em;
  position: relative;
	max-width: var(--contained-max-width);
  width: var(--contained-width);
	animation: word-card-in 1s ease-out;
	box-shadow: 0px 1px 2px 0px rgb(0, 0, 0, 0.30);
  will-change: opacity, transform;
}

.word-card * {
  font-family: 'Heebo';
}

.word-card-content {
  text-align: center;
}

.word-card-content p {
  text-align: justify;
  word-break: keep-all;
  margin: 0;
}

.word-card-content h2 {
	color: var(--peru-blue);
}

.word-card-content img {
  max-width: 100%;
  height: auto;
}

.center-content {
  text-align: center !important;
  word-break: unset;
}

.link-text button {
  margin-top: 1em;
  cursor: pointer;
  font-variant: small-caps;
  font-size: 1.1em;
}

@keyframes word-card-in {
  0% {
    opacity: 0;
    transform: translateY(3em);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

