:root {
  --peru-red: #D91023;
  --peru-yellow: #FCD116;
  --peru-blue: #007EA8;
  --seashell: seashell;
  --black: #363636;
  --contained-width: 60em;
  --contained-max-width: 90vw;
  --nav-height: 6em;
}

#app {
  min-height: 100vh;
  overflow-x: hidden;
  background: rgb(217,16,35);
  background: var(--peru-red);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

#nav-bar-top {
  width: 100%;
  z-index: 9999;
}

#nav-bar-container {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--nav-height);
  background: var(--seashell);
  box-shadow: 0px 2px 4px 0px rgb(0, 0, 0, 0.30);
}

#content-container {
  margin-top: 6em;
}

.content-header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content-header-inner {
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  background-color: var(--seashell);
  display: flex;
  flex-direction: column;
  border: 1px white solid;
  padding: 2em;
  width: 100vw;
  color: var(--black);
}

.content-header-inner p {
  text-align: center;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em 0;
}